import slide from 'components/slide/index'
import http from 'js/http'

export default {
  name: "index",
  props: ['selectId'],
  components: {
    slide
  },
  data() {
    return {
      nodeList: [],
      nodeTab: [],
      currentNodeId: '',
      page: 1,
      loadShow: true,
      total:''
    }
  },
  methods: {
    backVote() {
      this.$emit('close', true)
    },
    selectTab(item) {
      console.log(item)
      if (item.id === this.currentNodeId) return
      this.currentNodeId = item.id
      this.currentNodeId = item.id
      sessionStorage.setItem("chooseNodeId", item.id);
      this.page = 1
      this.nodeList = []
     /* this.loadShow = true
      this.$refs.vueLoad.onBottomLoaded();
      this.getNodeList()*/
      this.total = ''
      this.$refs.my_scroller.finishInfinite(false);
    },
    selectedNode(item) {
      this.$emit('selectedNode', item)
      this.backVote()
    },
    handleBottom() {
      /*this.page++
      this.getNodeList()*/
      if (this.currentNodeId === ''){
        this.getNodeTab(()=>{
          this.handleBottom()
        })
        return
      }

      if (this.total!==''&&this.nodeList.length >= parseInt(this.total)){
        this.$refs.my_scroller.finishInfinite(true);
        return
      }

      http.post('/node/vote', {
        id: this.currentNodeId,
        page: this.page,
        page_size: 10
      }, (res) => {
        if (this.loadShow) this.loadShow = false
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.nodeList = this.nodeList.concat(res.content.list)
        // this.counttime = res.content.counttime
        this.total = res.content.count
        this.page++
        this.$refs.my_scroller.finishInfinite(false);
      })
    },
    getNodeTab(cb) {
      http.post('/node', {}, (res) => {
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.nodeTab = res.content
        this.currentNodeId = sessionStorage.getItem('chooseNodeId') || res.content[0].id
        // this.getNodeList()
        cb()
      })
    },
    getNodeList() {
      http.post('/node/vote', {
        id: '1',
        page: this.page,
        page_size: 10
      }, (res) => {
        if (this.loadShow) this.loadShow = false
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.nodeList = this.nodeList.concat(res.content.list)
        this.counttime = res.content.counttime
        if (this.nodeList.length < parseInt(res.content.count)) {
          this.$refs.vueLoad.onBottomLoaded();
        } else {
          this.$refs.vueLoad.onBottomLoaded(false);
        }
      })
    },
  },
  created() {
    // this.getNodeTab()
  },
}



