import slide from 'components/slide/index'
import http from 'js/http'
import chooseNode from 'views/home/vote/chooseNode'
import validPayPsw from 'components/validPayPsw/index'
import {mapState, mapMutations, mapGetters} from 'vuex'
import {GetUrlParam} from 'js/mixin'

export default {
  name: "index",
  components: {
    slide,
    chooseNode,
    validPayPsw
  },
  data() {
    return {
      typeList: [],
      number: '',
      currentType: 1,
      typeInfo: {},
      nodeName: '',
      nodeId: '',
      chooseNodeShow: false,
      validPswShow: false,
    }
  },
  methods: {
    sbmVote() {
      if (!this.nodeId) {
        this.$vux.toast.show('请选择投票节点')
        return
      }
      if (this.number === '') {
        this.$vux.toast.show('请输入投票数量')
        return
      }
      if (!(/^[1-9]\d*$/.test(this.number))) {
        this.$vux.toast.show('请输入有效的投票数量')
        return
      }
      this.validPswShow = true
    },
    validPswSuccess(payPsw) {
      http.post('/vote/submit', {
        node_id: this.nodeId,
        type: this.currentType,
        number: this.number,
        pass: payPsw
      }, (res) => {
        let type = res.code === 0 ? 'success' : 'warn'
        this.$vux.toast.show({
          text: res.msg,
          toast: 3000,
          type: type
        })
        this.validPswShow = false
      })
    },
    getTypeList() {
      http.post('/vote/types', {}, (res) => {
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.typeList = res.content
        this.currentType = this.typeList[0].id
        this.getTypeInfo()
      })
    },
    selectedNode(item) {
      this.nodeName = item.name
      this.nodeId = item.id
    },
    changeType(id, index) {
      this.currentType = id
      this.getTypeInfo()
    },
    getTypeInfo() {
      http.post('/vote/type-info', {
        type: this.currentType
      }, (res) => {
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.typeInfo = res.content
      })
    },
    pageInt(){
      if (!this.loginMsg){
        this.$router.push({
          path: `/login`
        })
        return
      }
      /*console.log(this.$route.query.nodeId, this.$route.query.nodeName)
      let pathCode = GetUrlParam('nodeId')
      console.log(pathCode)*/
      if (!this.typeList.length){
        this.getTypeList()
      }
      this.nodeId = this.$route.query.nodeId
      this.nodeName = this.$route.query.nodeName

    }
  },
  created() {
    this.pageInt()
  },
  activated() {
    this.pageInt()
  },
  computed: {
    ...mapGetters([
      "loginMsg",
    ]),
  },
}



